import React from 'react';
import { Query } from 'react-apollo';
import { gql } from 'apollo-boost';

const getAuthorsQuery = gql`
  {
    authors {
      name
      id
    }
  }
`

class UsersList extends React.Component {
    render(){
        return(
            <React.Fragment>
                <Query query={getAuthorsQuery}>
                    <span>TEST</span>
                </Query>
            </React.Fragment>
        )
    }
}

export default UsersList;